import * as React from 'react'
import store from './store'
import uiStore from './uistore'
import { observer } from 'mobx-react'

//const search: string = '+symptoms+OR+sintomas+OR+pest+OR+praga+OR+doena+OR+disease+OR+inseto+OR+insect+OR+fungi+OR+fungi+OR+bactria';

function Result() {
  return (
    <div>
      <div className={store.completed && uiStore.searched ? '' : 'hidden'}>
        <br />
        <h3>
          Exigncias Fitossanitrias para o trnsito de {store.dados.prod} de {store.dados.hospVul}{' '}
          <i>({store.dados.hospSci})</i> do {store.dados.orig} para {store.dados.dest}
        </h3>
        <div className={store.empty ? '' : 'hidden'}>
          <br />
          <br />
          <span className="empty">
            <div>SEM EXIGNCIAS PARA O TRNSITO</div>
          </span>
          <br />
        </div>
        <span>{uiStore.searched}</span>

        {store.result.map((item, i) => {
          return (
            <div key={item.prag + i}>
              <hr />
              <h4 className="h4" style={{ textAlign: 'left', float: 'left' }}>
                <i>{item.prag}</i> - {item.pragc}
              </h4>
              <a
                className="small"
                target="_blank"
                href={'https://www.google.com.br/search?site=imghp&tbm=isch&q=' + item.prag}
                style={{ textAlign: 'right', float: 'right' }}
              >
                [FOTOS DA PRAGA]
              </a>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{'          '}
              <br />
              {item.files.map((file, iii) => (
                <div key={file.link + iii}>
                  <a target="_blank" href={'leg/' + file.link}>
                    {file.leg}
                  </a>
                  <br />
                </div>
              ))}
              <span className="small underline">{item.desc}</span>
              {item.exig.map((exig, ii) => {
                return (
                  <div style={{ margin: '6px' }} key={exig + ii}>
                    <span title={'De: ' + item.orig + ' para ' + item.dest}>
                      {ii + 1} - {exig}
                    </span>
                  </div>
                )
              })}
            </div>
          )
        })}

        <div className={store.empty ? 'hidden' : ''}>
          <hr />
          <h4 className="h4">TRNSITO NACIONAL DE PARTIDA IMPORTADA</h4>
          <div style={{ margin: '6px' }}>
            <span>
              1  SE A PARTIDA AINDA NO FOI INTERNALIZADA PELO MAPA E ESTIVER EM TRNSITO A UMA
              REA ALFANDEGADA NO INTERIOR DO BRASIL:
            </span>
            <br />
            <span>- Certificado Fitossanitrio ou Certificado Fitossanitrio de Reexportao;</span>
          </div>
          <div style={{ margin: '6px' }}>
            <span>
              2  SE A PARTIDA J FOI INTERNALIZADA PELO MAPA E ESTIVER EM TRNSITO AO PONTO DE
              DESTINO DECLARADO NA IMPORTAO:
            </span>
            <br />
            <span>
              - Cpia autenticada do Certificado Fitossanitrio ou do Certificado Fitossanitrio de
              Reexportao;{' '}
            </span>
            <br />
            <span>
              - Original ou cpia autenticada do Requerimento para Fiscalizao de Produtos
              Agropecurios, emitido pelo MAPA;
            </span>
          </div>
          <div style={{ margin: '6px' }}>
            <span>
              3  SE A PARTIDA J FOI INTERNALIZADA PELO MAPA E ESTIVER SAINDO DO DESTINO DECLARADO
              NA IMPORTAO, EM DIREO A QUALQUER UF:
            </span>
            <br />
            <span>- Cumprir os requisitos fitossanitrios para o trnsito interestadual.</span>
          </div>
          <hr />
          <h4 className="h4">TRNSITO NACIONAL DE PARTIDA EXPORTADA</h4>
          <div style={{ margin: '6px' }}>
            <span>
              1  SE A PARTIDA J ESTIVER COM CERTIFICADO FITOSSANITRIO NO INTERIOR DO BRASIL, EM
              TRNSITO PARA PONTO DE EGRESSO:
            </span>
            <span>- Certificado Fitossanitrio.</span>
          </div>
          <hr />
          <h4 className="h4">LEGISLAO GERAL</h4>
          <a target="_blank" href="leg/IN28-2016.pdf">
            Instruo Normativa MAPA N 28, de 24 de agosto de 2016
          </a>
          <br />
          <br />
          <a target="_blank" href="leg/IN59-2013.pdf">
            Instruo Normativa MAPA N 59, de 18 de dezembro de 2013
          </a>
          <br />
          <hr />
        </div>
        <div style={{ textAlign: 'center' }}>
          <button
            onClick={uiStore.handleMenu.bind(undefined, 'Voltar')}
            className="form-button"
            disabled={false}
          >
            Voltar
          </button>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          <button
            onClick={uiStore.handleMenu.bind(undefined, 'Nova')}
            className="form-button"
            disabled={false}
          >
            Nova Consulta
          </button>
        </div>
      </div>
    </div>
  )
}

export default observer(Result)

